.nav {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    height: 30px;
    z-index: 1;

    display: flex;
    justify-content: space-between;
    /* ANIMATION EFFECT*/
    transition-timing-function: ease-in;
    transition: all 0.5s;
    
  }

.nav__black {
  background-color: #111;
}
  
  .nav__logo {
      font-weight: 500px;
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
  }